"use client";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState, useEffect } from "react";
import { useTheme } from "next-themes";
import Image from "next/image";
import { faMoon, faSun } from "@fortawesome/free-regular-svg-icons";

export default function ThemeSwitch() {
  const [mounted, setMounted] = useState(false);
  const { setTheme, resolvedTheme } = useTheme();

  useEffect(() => setMounted(true), []);

  if (!mounted)
    return (
      <Image
        src="data:image/svg+xml;base64,"
        width={36}
        height={36}
        sizes="36x36"
        alt="Loading Light/Dark Toggle"
        priority={false}
        title="Loading Light/Dark Toggle"
      />
    );

  if (resolvedTheme === "dark") {
    return <FontAwesomeIcon icon={faSun} onClick={() => setTheme("light")} className="text-sm cursor-pointer hover:scale-105 transition-all" />;
  }

  if (resolvedTheme === "light") {
    return <FontAwesomeIcon icon={faMoon} onClick={() => setTheme("dark")} className="text-sm cursor-pointer hover:scale-105 transition-all" />;
  }
}
